/*benefits*/
/* General Styles */
.section-header {
  text-align: center;
}

/* Gradient Background Section */
.gradient-background {
  background: linear-gradient(90deg, rgb(2, 43, 73) 0%, rgba(23, 24, 38, 1) 100%);
  box-shadow: 16px 19px 51px -2px #000;
  margin: 2rem 6rem;
  border-radius: 0.6875rem;
  padding: 2rem 2rem 3rem;
}
@media only screen and (max-width: 767px) {

.gradient-background {
  margin: 1rem 1rem;
  padding: 2rem 0rem 2rem 2rem;
  width: 90%;
}
}
/* Content Section */
.benefit-section {
  display: flex;
  flex-direction: row;
  padding: 0rem 2rem;
  border-left: 0.4rem solid #FF991B;
}

/* Text Paragraph */
.text-paragraph {
  color: #C2D6FF;
  font-family: Calibri;
  font-size: 1.0625rem;
  font-weight: 400;
  line-height: 2rem;
  margin: 0;
}

/* Highlighted Text */
.highlighted-text {
  color: #FF991B;
}

/* Heading */
.heading {
  color: #FF991B;
  font-family: Calibri;
  font-size: 1.875rem;
  font-weight: 700;
  line-height: 1.875rem;
  margin: 0;
  margin-top: 15px;
  margin-bottom: 8px;
  padding: 1.5rem 0rem;
}
@media only screen and (max-width: 767px) {
  .heading{
  font-size: 1.7rem;
  font-weight: 650;
  
  }
}

/* Benefits Section */
.benefits-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

/* Media Query for small screens (max-width: 767px) */
@media only screen and (max-width: 767px) {
  .benefit-section,
  .benefits-section {
    flex-direction: column;
    align-items: center;
  }
  
}
/*End Benefits*/
/* BenefitItem Styles */
.benefit-item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-top: 5px;
  margin-bottom: 10px;
  border-left: 0.4rem solid #FF991B;
  padding: 0 2rem;
}

/* Heading Styles */
.benefit-item h3 {
  color: #FFF;
  font-family: Calibri;
  font-size: 1.375rem;
  font-weight: 700;
  line-height: 1.875rem;
  margin: 0;
}

/* Description Styles */
.benefit-item p {
  color: #FFF;
  font-family: Calibri;
  font-size: 0.950rem;
  font-weight: 400;
  line-height: 1.125rem;
  margin: 0;
  padding: 1rem 0;
}
/*End BenfitItem*/